html {
  box-sizing: border-box;
}
*, ::after, ::before{
  box-sizing: inherit;
}
body {
  font-family: "Roboto", sans-serif;
  line-height: 1.5;   
  background-image: url("money.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat; 
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}
